@import url(https://fonts.googleapis.com/css?family=Spartan);
@import url(https://fonts.googleapis.com/css?family=Cabin);
@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --secondary-purple: #3D4267;
  --primary-gray: #292929;
  --white: #E8E9F3;
}
/* OVERALL */

body {
	background-color: #3D4267;
	background-color: var(--secondary-purple);
	font-family: 'Spartan', sans-serif;
	
}

h1 {
	font-size: 28pt;
	font-weight: 800;
	font-family: 'Questrial', sans-serif;
}

/* LINKS */

a {
	text-decoration: none;
	font-weight: 600;
}

.strike {
	color: #E8E9F3;
	color: var(--white);
	position: relative;
	line-height: 30px;
}

.strike:hover {
	cursor: pointer;
	-webkit-animation: strikethrough;
	        animation: strikethrough;
	color: #E8E9F3;
	color: var(--white);
}

@-webkit-keyframes strikethrough {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	100% {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}

@keyframes strikethrough {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	100% {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}

.strike:hover:after {
  content: ' ';
  position: absolute;
  display: block;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #E8E9F3;
  background: var(--white);
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-animation: strikethrough .3s ease-out;
          animation: strikethrough .3s ease-out;
}

.strike-purple {
	color: #3D4267;
	color: var(--secondary-purple);
}

.strike-purple:hover:after {
	background-color: #3D4267;
	background-color: var(--secondary-purple);
}

.home-container {
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
}
body {
